.cardbox-primary {
  background-color: transparent;
  // background-image: linear-gradient(160deg, #45F5A1 32%, @primary-color 100%);
  background-image: linear-gradient(160deg, @primary-color 15%, @primary-color 100%);
  position: relative;
  .cardbox-overlay {
    background-image: url(https://demo.themovation.com/stratus/wp-content/uploads/2020/04/overlay-3.jpg);
    background-position: center left;
    background-size: cover;
    opacity: 0.15;
    transition: background 0.3s, border-radius 0.3s, opacity 0.3s;
  }
}
.cardbox-yellow {
  background-color: transparent;
  background-image: linear-gradient(160deg, rgb(252, 213, 53) 15%, darken(rgb(252, 213, 53), 5%) 100%);
  position: relative;
  .cardbox-overlay {
    background-image: url(https://demo.themovation.com/stratus/wp-content/uploads/2020/04/overlay-3.jpg);
    background-position: center left;
    background-size: cover;
    opacity: 0.15;
    transition: background 0.3s, border-radius 0.3s, opacity 0.3s;
  }
}
