// @brand-success: #01d394;
@brand-error: #ff5656;
@brand-warn: @warning-color;
@brand-info: blue; // TODO

// brand-light
@brand-primary-light: fade(@primary-color, 10%);
@brand-secondary-light: fade(@brand-secondary, 15%);
@brand-up-light: fade(@brand-up, 15%);
@brand-down-light: fade(@brand-down, 15%);
@brand-success-light: fade(@brand-success, 15%);
@brand-error-light: fade(@brand-error, 15%);
@brand-warn-light: fade(@brand-warn, 15%);
@brand-info-light: fade(@brand-info, 15%);
// font-family
@ff-text: 'Roboto, Open Sans, SF Pro SC, SF Pro Text, Helvetica Neue, Helvetica, Arial, PingFang SC, Hiragino Sans GB, Heiti SC, Microsoft YaHei, sans-serif';
@ff-number: 'Roboto, Open Sans, SF Pro SC, SF Pro Text, Helvetica Neue, Helvetica, Arial, PingFang SC, Hiragino Sans GB, Heiti SC, Microsoft YaHei, sans-serif';
// border-radius
@br-main: 50em;
// bg
@bg-body: #f2f3f8;
@bg-card: #fff; // TODO
@bg-card-header: #fff; // TODO
@bg-card-body: @brand-primary-light; // TODO
@bg-modal: @bg-card;
@bg-navbar: #fff; // TODO
@bg-input: transparent;
@bg-footer: transparent;
@bg-layer: #fff;
@bg-tooltip: #fff;
@bg-loading: rgba(0, 0, 0, 0.4);
@bg-table-hover: rgba(0, 0, 0, 0.07);
@bg-list-hover: rgba(0, 0, 0, 0.07);
@bg-scrollbar: rgba(0, 0, 0, 0.1);
// bc
@bc-grey: #3000;
@bc-grey-light: #f2f2f2; // TODO
@bc-grey-lighter: #f2f2f2; // TODO
@bc-input: #d8dae6;
// fc
@fc-bw: #000;
@fc-grey-dark: #000;
@fc-grey: #000;
// @fc-grey-light: rgba(0,0,0,0.6);
@fc-grey-light: rgb(125, 146, 166);
@fc-grey-lighter: rgba(0, 0, 0, 0.45);
@fc-icon: @fc-grey;
// navbar
@fc-typo-navbar: #fff;
@fc-typo-navbar-hover: @brand-primary;
@fc-typo-navbar-active: @brand-primary;

.fc-type(@type: ~'bw'; @color: @fc-bw;);
.fc-type(@type: ~'grey-dark'; @color: @fc-grey-dark; ); // 新版本已废弃，暂时保留以兼容老版本
.fc-type(@type: ~'grey'; @color: @fc-grey; @hover:@brand-primary;);
.fc-type(@type: ~'grey-light'; @color: @fc-grey-light;@hover:@fc-grey; );
.fc-type(@type: ~'grey-lighter'; @color: @fc-grey-lighter;);
.fc-type(@type: ~'primary'; @color: @brand-primary;@hover:lighten(@brand-primary,10%); );
.fc-type(@type: ~'up'; @color: @brand-up;);
.fc-type(@type: ~'down'; @color: @brand-down;);
.fc-type(@type: ~'success'; @color: @brand-success; @hover:lighten(@brand-up,10%););
.fc-type(@type: ~'error'; @color: @brand-error; @hover:lighten(@brand-up,10%););
.fc-type(@type: ~'warn'; @color: @brand-warn; @hover:lighten(@brand-up,10%););
.fc-type(@type: ~'info'; @color: @brand-info; @hover:lighten(@brand-up,10%););
.fc-type(@type: ~'icon'; @color: @fc-icon; );

