.isDark {
  @brand-primary-light:fade (@brand-primary,15);
  @bg-card: #252b2d;
  @bg-card-light: lighten(@bg-card, 8%);
  @fc-grey: #fff;
  @fc-grey-light: rgba(255, 255, 255, 0.8);
  @fc-grey-lighter: rgba(255, 255, 255, 0.55);

  // .bg-card{
  //     background-color: transparent !important;;
  //     background-image: linear-gradient(180deg, #131A14 0%, #252b2d 100%) !important;
  // }
  .bg-type(@type: ~'card'; @color: @bg-card; );
  .bg-type(@type: ~'card-light'; @color: @bg-card-light; );
  .bg-type(@type: ~'primary-light'; @color:@brand-primary-light; );

  .fc-type(@type: ~'grey'; @color: @fc-grey; @hover:@brand-primary;);
  .fc-type(@type: ~'grey-light'; @color: @fc-grey-light; @hover:@brand-primary;);
  .fc-type(@type: ~'grey-lighter'; @color: @fc-grey-lighter; @hover:@brand-primary;);

  .bb-grey-light {
    border-bottom: 1px solid lighten(@bg-card, 3%) !important;
  }
  .bt-grey-light {
    border-top: 1px solid lighten(@bg-card, 3%) !important;
  }
  .border-grey-light {
    border: 1px solid lighten(@bg-card, 3%) !important;
  }
  .ant-modal-content {
    background-color: @bg-card;
    .ant-modal-header {
      background: transparent;
    }
  }

  .ant-card {
    background-color: @bg-card;
    // background-color: rgba(0,0,0,0.5);
    // border: 1px solid rgba(255,255,255,0.1) !important;
    margin-bottom: 1px;
    .ant-card-head {
      background-color: rgba(0, 0, 0, 0.15);
      border-radius: 6px 6px 0 0;
      border-bottom: none;
      // border-bottom:1px solid rgba(255,255,255,0.1);
    }
    .ant-card-head-wrapper {
    }
  }
  .ant-table {
    // background-color: @bg-card;
    background-color: transparent;
    // color: lighten(@fc-grey-light, 5%);
    color: @fc-grey-light;
  }
  .ant-table-thead > tr > th {
    // background-color: darken(@bg-card, 2%);
    background-color: transparent;
  }
  .ant-table-tbody > tr {
    &:hover {
      & > td {
        background-color: darken(@bg-card, 5%);
      }
    }
  }
  .ant-table-thead > tr > th,
  .ant-table tfoot > tr > th,
  .ant-table-tbody > tr > td,
  .ant-table tfoot > tr > td {
    .bb-grey-light;
    .fc-grey-light;
  }

  .ant-card-head-title {
    color: @fc-grey;
  }
  .ant-tabs-top > .ant-tabs-nav::before {
    .bb-grey-light;
  }
  .ant-tabs-tab-active {
    .tab-item {
      .fc-grey;
    }
  }
  .ant-input {
    background: @bg-card-light !important;
    // border:none;
    color: #fff;
    border: none;
  }
  .ant-input-affix-wrapper {
    background: @bg-card-light !important;
  }
  .ant-btn-primary[disabled],
  .ant-btn-primary[disabled]:hover,
  .ant-btn-primary[disabled]:focus,
  .ant-btn-primary[disabled]:active {
    color: rgba(255, 255, 255, 0.5);
    background: @bg-card-light;
    border-color: @bg-card-light;
    text-shadow: none;
    box-shadow: none;
  }

  .vault-detail-page {
    .ant-card {
      // background:transparent !important;
      .ant-card-head {
      }
    }
    .ant-card-head {
      .ant-card-head-title {
        padding: 10px 0 !important;
        font-size: 18px !important;
      }
    }
    .ant-card-body {
      padding: 16px;
    }
  }
}

.ant-table-thead
  > tr
  > th:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before {
  width: 0px;
}
